@import url(../../node_modules/normalize.css/normalize.css);
html,
body,
div,
span,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
address,
img,
small,
strong,
sub,
sup,
b,
i,
ol,
ul,
li,
form,
label,
article,
aside,
footer,
header,
nav,
section,
time,
audio,
video {
  margin: 0;
  outline: 0;
  border: 0;
  padding: 0;
  background: transparent;
}

html {
  word-break: normal;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  scroll-behavior: smooth;
}

ul[class],
ol {
  list-style: none;
}

a {
  outline: 0;
  text-decoration: none;
  background: transparent;
  text-decoration-skip-ink: none;
}

address {
  font-style: normal;
}

input,
button,
textarea,
select {
  vertical-align: middle;
  margin: 0;
  border: none;
  border-radius: 0;
  padding: 0;
  font: inherit;
  color: inherit;
  background: transparent;
}

img {
  display: block;
  max-width: 100%;
}

:focus {
  outline: none;
}

body {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 400;
}

.title {
  font-family: "Lora", sans-serif;
  font-weight: 500;
}

/* source-sans-pro-regular - latin_cyrillic */
@font-face {
  font-family: "Source Sans Pro";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("../fonts/source-sans-pro-v14-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/source-sans-pro-v14-latin_cyrillic-regular.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* lora-500 - cyrillic */
@font-face {
  font-family: "Lora";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local(""), url("../fonts/lora-v17-cyrillic-500.woff2") format("woff2"), url("../fonts/lora-v17-cyrillic-500.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  height: 100vh;
  font-size: 16px;
  line-height: 1.5;
  text-shadow: -1px -1px 1px rgba(255, 255, 255, 0.2), 1px 1px 1px rgba(0, 0, 0, 0.6);
  color: #f5f5f5;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1230px;
}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 30px 0;
  font-size: 48px;
  white-space: nowrap;
  color: #073b4c;
}

@media only screen and (max-width: 767px) {
  .title {
    top: 30%;
    font-size: 2rem;
  }
}

.hover-right .split--right {
  width: 75%;
}

.hover-right .split--left {
  width: 25%;
}

.hover-left .split--right {
  width: 25%;
}

.hover-left .split--left {
  width: 75%;
}

.hover-left .contacts__content {
  transform: translateX(100vh);
}

.portfolio {
  position: relative;
  min-width: 100%;
  min-height: 100vh;
  background-color: #fff;
}

.split {
  position: absolute;
  overflow: hidden;
  width: 50%;
  min-height: 800px;
  max-height: 100vh;
}

.split.split--left {
  left: 0;
  background-color: #e4f9f5;
}

.split.split--right {
  right: 0;
  background-color: #30e3ca;
}

.split.split--left, .split.split--right, .split.split--left::before, .split.split--right::before {
  transition: all 0.8s ease-in-out;
}

.contacts__inner {
  display: flex;
  justify-content: space-between;
  margin: 0 10%;
}

.contacts__body {
  margin-right: 30px;
}

.contacts__content {
  margin: 0 20px;
  border-radius: 6px;
  padding: 10px;
  min-width: 280px;
  max-width: 50%;
  color: #e4f9f5;
  background-color: rgba(0, 0, 0, 0.1);
  transition: all 0.6s;
}

.contacts__list {
  display: flex;
  align-items: center;
  justify-content: center;
}

.contacts__item {
  transition: transform 0.4s;
  cursor: pointer;
}

.contacts__item:not(:last-child) {
  margin-right: 15px;
}

.contacts__item:hover {
  transform: scale(1.08);
}

.contacts__link {
  font-size: 1rem;
  color: #ececec;
}

.contacts__icon {
  max-width: 50px;
  max-height: 50px;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 90vw;
  max-height: 100vh;
}

.cards__item {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  flex: 0.5;
  margin: 10px;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
  min-width: 250px;
  min-height: 300px;
  max-width: 500px;
  color: #fff;
  transition: all 0.7s ease-in;
  cursor: pointer;
}

.cards__item.cards__item--active {
  flex: 5;
}

.cards__item.cards__item--active .cards__title {
  opacity: 1;
  transition: opacity 0.4s ease-in 0.4s;
}

.cards__item.cards__item--active .cards__link {
  visibility: visible;
}

.cards__title {
  position: absolute;
  left: 10px;
  right: 10px;
  bottom: 20px;
  border-radius: 5px;
  padding: 5px 7px;
  font-size: 20px;
  background-color: #000;
  opacity: 0;
}

.cards__link {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  visibility: hidden;
  cursor: pointer;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto 30px;
  color: #073b4c;
}

.info__title {
  font-size: 18px;
  text-align: center;
}

.info__img-wrapper {
  margin-bottom: 20px;
  max-width: 250px;
  max-height: 500px;
}

.info__img {
  border-radius: 6px;
  object-fit: cover;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em, rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
}
