.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto 30px;
  color: $title;

  &__title {
    font-size: 18px;
    text-align: center;
  }

  &__img-wrapper {
    margin-bottom: 20px;
    max-width: 250px;
    max-height: 500px;
  }

  &__img {
    border-radius: 6px;
    object-fit: cover;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em,
      rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em,
      rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
  }
}
