html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  height: 100vh;
  font-size: 16px;
  line-height: 1.5;
  text-shadow: -1px -1px 1px rgba(255, 255, 255, 0.2),
    1px 1px 1px rgba(0, 0, 0, 0.6);
  color: #f5f5f5;
  @extend %s400;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1230px;
}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 30px 0;
  font-size: 48px;
  white-space: nowrap;
  color: $title;
  @include mq("768") {
    top: 30%;
    font-size: 2rem;
  }
  @extend %l500;
}

.hover-right {
  & .split--right {
    width: 75%;
  }

  & .split--left {
    width: 25%;
  }
}

.hover-left {
  & .split--right {
    width: 25%;
  }

  & .split--left {
    width: 75%;
  }

  & .contacts__content {
    transform: translateX(100vh);
  }
}

.portfolio {
  position: relative;
  min-width: 100%;
  min-height: 100vh;
  background-color: #fff;
}

.split {
  position: absolute;
  overflow: hidden;
  width: 50%;
  min-height: 800px;
  max-height: 100vh;

  &.split--left {
    left: 0;
    background-color: $bg2;
  }

  &.split--right {
    right: 0;
    background-color: $bg1;
  }

  &.split--left,
  &.split--right,
  &.split--left::before,
  &.split--right::before {
    transition: all 0.8s ease-in-out;
  }
}
