.contacts {
  &__inner {
    display: flex;
    // flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 10%;
  }

  &__body {
    margin-right: 30px;
  }

  &__content {
    margin: 0 20px;
    border-radius: 6px;
    padding: 10px;
    min-width: 280px;
    max-width: 50%;
    color: $bg2;
    background-color: rgba(0, 0, 0, 0.1);
    transition: all 0.6s;
  }

  &__text {
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__item {
    transition: transform 0.4s;
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 15px;
    }

    &:hover {
      transform: scale(1.08);
    }
  }

  &__link {
    font-size: 1rem;
    color: #ececec;
  }

  &__icon {
    max-width: 50px;
    max-height: 50px;
  }
}
