.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 90vw;
  max-height: 100vh;

  &__item {
    @include bg();
    position: relative;
    flex: 0.5;
    margin: 10px;
    // border: 1px solid #000;
    border-radius: 6px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px,
      rgba(0, 0, 0, 0.22) 0px 10px 10px;
    min-width: 250px;
    min-height: 300px;
    max-width: 500px;
    color: #fff;
    transition: all 0.7s ease-in;
    cursor: pointer;

    &.cards__item--active {
      flex: 5;

      .cards__title {
        opacity: 1;
        transition: opacity 0.4s ease-in 0.4s;
      }

      .cards__link {
        visibility: visible;
      }
    }
  }

  &__title {
    position: absolute;
    left: 10px;
    right: 10px;
    bottom: 20px;
    border-radius: 5px;
    padding: 5px 7px;
    font-size: 20px;
    background-color: #000;
    opacity: 0;
  }

  &__link {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    visibility: hidden;
    cursor: pointer;
  }
}
